import React, {useEffect, useState} from 'react';
import {Card, Button, Grid, Divider} from '@mui/material';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CodeIcon from '@mui/icons-material/Code';
import PropTypes from 'prop-types';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import AppAnimate from '../../../../@crema/core/AppAnimate';
import TextField from '@mui/material/TextField';
import {Fonts} from '../../../../shared/constants/AppEnums';

import {
  addSalePoint,
  generateSPID,
} from '../../../../@crema/services/auth/firebase/firebase';
import {useIntl} from 'react-intl';
const AddSellingpoint = ({
  // title,
  description,
  source,
}) => {
  const [formData, updateFormData] = useState();
  const [viewSource, setToggleViewSource] = useState(false);
  const [animation, setAnimation] = useState(false);
  const [vendorId, setVendorId] = useState();
  const {messages} = useIntl();

  useEffect(async () => {
    let id = await generateSPID();
    setVendorId(id);
  }, [vendorId]);

  const handleChange = (e) => {
    updateFormData({
      ...formData,

      [e.target.name]: e.target.value.trim(),
    });
  };

  const addSPoint = (e) => {
    e.preventDefault();
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    today = mm + '/' + dd + '/' + yyyy;
    const form = {
      id: vendorId,
      name: formData.name,
      phone_Number: formData.Phone_Number,
      email: formData.email,
      address: formData.address,
      password: formData.password,
      time: Date.now(),
      addDate: today,
      Role: 'salePoint',
    };
    console.log(form);
    addSalePoint(vendorId, form);
  };

  return (
    <form onSubmit={(e) => addSPoint(e)}>
      <AppAnimate animation='transition.slideUpIn' delay={200}>
        <Card>
          <CardHeader
            sx={{
              py: 4,

              pb: 1,
              px: 5,
              display: 'flex',
              alignItems: 'center',
              minHeight: 50,
              boxSizing: 'border-box',
              '& .MuiTypography-h5': {
                fontSize: 14,
                fontWeight: Fonts.BOLD,
                marginBottom: 0.25,
              },
            }}
            // title={title}
            subheader={description}
            root={{
              subheader: {
                fontSize: 13,
              },
            }}
            action={
              source ? (
                <Box>
                  <IconButton
                    aria-label='view code'
                    onClick={() => {
                      if (animation) {
                        setAnimation(!animation);
                        setTimeout(() => setToggleViewSource(!viewSource), 400);
                      } else {
                        setAnimation(!animation);
                        setToggleViewSource(!viewSource);
                      }
                    }}
                    size='large'
                  >
                    <CodeIcon />
                  </IconButton>
                </Box>
              ) : null
            }
          />

          <CardContent sx={{px: 10, pt: 1}}>
            <Grid container item xs={12} sm={5} spacing={4} textAlign={'start'}>
              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  id='outlined-basic'
                  name='name'
                  label={['Full Name']}
                  variant='outlined'
                  onChange={handleChange}
                  required
                />
              </Grid>

              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  type='number'
                  id='Phone_Number'
                  name='Phone_Number'
                  label={['Phone Number']}
                  variant='outlined'
                  onChange={handleChange}
                  required
                />
              </Grid>
              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  id='email'
                  name='email'
                  label={['Email']}
                  variant='outlined'
                  onChange={handleChange}
                  required
                />
              </Grid>
              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  id='password'
                  name='password'
                  label={['Password']}
                  type='password'
                  variant='outlined'
                  onChange={handleChange}
                  required
                />
              </Grid>

              <Grid item xs={12} style={{minHeight: '100px'}}>
                <TextField
                  style={{width: '100%'}}
                  id='address'
                  name='address'
                  label={['Address']}
                  variant='outlined'
                  size='large'
                  inputProps={{style: {minHeight: 70}}}
                  onChange={handleChange}
                  required
                />
              </Grid>

              <Grid item sx={0} sm={1}>
                <Divider
                  orientation='horizontal'
                  style={{
                    marginRight: '50%',
                    marginLeft: '50%',
                    height: '100%',
                    width: '2px',
                  }}
                />
              </Grid>

              <Grid container xs={12} sm={6} item spacing={2}>
                <Grid
                  item
                  maxHeight={{xs: '170px', lg: '250px'}}
                  overflow={'auto'}
                  xs={12}
                ></Grid>
              </Grid>
            </Grid>
            <Button type='submit' variant='contained' color='primary'>
              {messages['sidebar.mangments.AddSellingpoint']}
            </Button>
          </CardContent>
        </Card>
      </AppAnimate>
    </form>
  );
};

AddSellingpoint.propTypes = {
  source: PropTypes.any,
  // title: PropTypes.node.isRequired,
  description: PropTypes.node,
  // buttonCompMethod:  PropTypes.any.isRequired,
};

export default AddSellingpoint;
